"use client";

import { useState, useRef, useEffect } from "react";
import { useRouter } from "next/navigation";
import { Bell, MessageSquare, Search, Settings, LogOut, User } from "lucide-react";

export default function Navbar() {
  const [query, setQuery] = useState("");
  const [showDropdown, setShowDropdown] = useState(false);
  const dropdownRef = useRef<HTMLDivElement>(null);
  const router = useRouter();

  const handleKeyDown = (e: React.KeyboardEvent<HTMLInputElement>) => {
    if (e.key === "Enter") {
      e.preventDefault();
      router.push(`/search?query=${query}`);
      setQuery(""); // Clear input after search
    }
  };

  const handleLogout = () => {
    router.push("/login"); // Redirect to login page
    setShowDropdown(false);
  };

  const handleProfileClick = () => {
    router.push("/profiles"); // Redirect to profile page
    setShowDropdown(false);
  };

  // Close dropdown when clicking outside
  useEffect(() => {
    function handleClickOutside(event: MouseEvent) {
      if (dropdownRef.current && !dropdownRef.current.contains(event.target as Node)) {
        setShowDropdown(false);
      }
    }

    document.addEventListener("mousedown", handleClickOutside);
    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
    };
  }, []);

  return (
    <div className="flex items-center justify-between bg-white p-4 shadow-md">
      {/* Logo / Greeting */}
      <div>
        <h1 className="text-2xl font-bold">Hello, Guest</h1>
      </div>

      {/* Search Bar */}
      <div className="relative w-[400px] hidden md:block">
        <Search className="absolute left-3 top-1/2 h-4 w-4 -translate-y-1/2 text-gray-400" />
        <input
          value={query}
          onChange={(e) => setQuery(e.target.value)}
          onKeyDown={handleKeyDown}
          placeholder="What do you want to eat today..."
          className="w-full rounded-full bg-white pl-10 pr-4 py-2 text-sm outline-none border border-gray-300 focus:ring-2 focus:ring-red-400"
        />
      </div>

      {/* Icons and Dropdown */}
      <div className="flex items-center gap-4">
        <button className="p-2 rounded-full bg-white border hover:bg-gray-100">
          <MessageSquare className="h-5 w-5 text-gray-600" />
        </button>
        <button className="p-2 rounded-full bg-white border hover:bg-gray-100">
          <Bell className="h-5 w-5 text-gray-600" />
        </button>

        {/* Settings Dropdown */}
        <div className="relative" ref={dropdownRef}>
          <button
            className="p-2 rounded-full bg-white border hover:bg-gray-100"
            onClick={() => setShowDropdown(!showDropdown)}
          >
            <Settings className="h-5 w-5 text-gray-600" />
          </button>

          {showDropdown && (
            <div className="absolute right-0 mt-2 w-48 rounded-md shadow-lg bg-white ring-1 ring-black ring-opacity-5">
              <div className="py-1">
                <button
                  onClick={handleProfileClick}
                  className="flex items-center px-4 py-2 text-sm text-gray-700 hover:bg-gray-100 w-full"
                >
                  <User className="h-4 w-4 mr-2" />
                  Profile
                </button>
                <button
                  onClick={handleLogout}
                  className="flex items-center px-4 py-2 text-sm text-gray-700 hover:bg-gray-100 w-full"
                >
                  <LogOut className="h-4 w-4 mr-2" />
                  Logout
                </button>
              </div>
            </div>
          )}
        </div>
      </div>
    </div>
  );
}
